"use client";

import { useEffect, useState } from "react";


export default function AdminCategories() {
  const [categories, setCategories] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [name, setName] = useState("");
  const [type, setType] = useState("genre");

  const fetchCategories = async () => {
    setLoading(true);
    try {
      const res = await getCategories();
      setCategories(res.data);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  const handleCreate = async () => {
    await createCategory({ name, type });
    setName("");
    setType("genre");
    fetchCategories();
  };

  const handleDelete = async (id: number) => {
    if (!confirm("هل أنت متأكد من الحذف؟")) return;
    await deleteCategory(id);
    fetchCategories();
  };

  return (
    <div className="p-6">
      <h2 className="text-2xl font-bold mb-4">إدارة التصنيفات</h2>

      {/* إنشاء تصنيف جديد */}
      <div className="flex gap-2 mb-6">
        <input
          placeholder="اسم التصنيف"
          value={name}
          onChange={(e) => setName(e.target.value)}
          className="p-2 rounded bg-gray-700 text-white"
        />
        <select
          value={type}
          onChange={(e) => setType(e.target.value)}
          className="p-2 rounded bg-gray-700 text-white"
        >
          <option value="genre">نوع</option>
          <option value="topic">موضوع</option>
          <option value="age">الفئة العمرية</option>
        </select>
        <button onClick={handleCreate} className="bg-red-600 px-4 rounded text-white">إضافة</button>
      </div>

      {/* جدول التصنيفات */}
      {loading ? (
        <p>جارٍ التحميل...</p>
      ) : (
        <table className="min-w-full bg-gray-800 text-white rounded-lg">
          <thead>
            <tr>
              <th className="p-2">#</th>
              <th className="p-2">الاسم</th>
              <th className="p-2">النوع</th>
              <th className="p-2">الإجراءات</th>
            </tr>
          </thead>
          <tbody>
            {categories.map((cat, idx) => (
              <tr key={cat.id} className="border-t border-gray-700">
                <td className="p-2">{idx + 1}</td>
                <td className="p-2">{cat.name}</td>
                <td className="p-2">{cat.type}</td>
                <td className="p-2">
                  <button
                    onClick={() => handleDelete(cat.id)}
                    className="bg-red-600 px-2 rounded"
                  >
                    حذف
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
